@font-face{
   font-family:'moon-ar';
   src:url('../fonts/ar/ElMessiri-VariableFont_wght.ttf');
}

@font-face{
   font-family:'moon-en';
   src:url('../fonts/en/Glory-VariableFont_wght.ttf');
}

*{
  margin:0;
  padding:0;
}

html[lang="ar"] *{
   font-family:'moon-ar' !important;
}
html[lang="en"] *{
   font-family:'moon-en' !important;
}
body{
  background:#023047;
}
a{
  text-decoration:none;
  font-size:20px;
  font-weight:bold;
}

a, p, h1, h2, button{
  color:#fff;
}

button{
  outline:none;
  border:none;
  font-size:20px;
  font-weight:bold;
  cursor:pointer;
}
nav{
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:20px 0px
}
nav .logo a{
  display:flex;
  justify-content:space-evenly;
  align-items:center;
  font-size:25px;
  margin:0 50px;
}
nav .logo h1{
    margin:0px 20px
}
nav .navbar{
    display:flex;
    justify-content:space-evenly;
    align-items:center;

}
nav .navbar ul{
    display:flex;
    list-style:none;
}
nav .navbar ul li a{
    margin:0 20px;
}
nav .navbar ul li a:hover{
    color:#FBB040;
    transition:0.3s ease-in-out;
}
nav .navbar ul li a:focus{
    color:#FBB040;
}
nav .navbar .otherItems{
    margin:0 50px 0 60px;
}
nav .navbar .otherItems .lang button{
    background:#0CA48D;
    border-radius:40px;
    padding:10px;
}
nav .toggle {
    transform: rotateY(180deg);
    display: none;
    cursor: pointer;
    background:#858585;
    padding:5px;
    border-radius:5px;
    position:relative;
}
nav .toggle div {
    width: 35px;
    height: 4px;
    background-color: #fff;
    margin: 6px 0;
    border-radius: 50px;
    transition: 0.7s ease-in-out;
}
nav .toggle div:nth-child(1){
    width: 20px;
}
nav .toggle div:nth-child(2){
    width: 27px;
}
nav .toggle.change div{
    width: 20px !important;
    transition: 0.7s ease-in-out;
}
html[lang="en"] nav .toggle.change div:nth-child(1) {
    -webkit-transform: rotate(-45deg) translate(-2px, 12px);
    transform: rotate(-45deg) translate(-2px, 12px);
}
html[lang="en"] nav .toggle.change div:nth-child(2) {
    opacity: 0;
}
html[lang="en"] nav .toggle.change div:nth-child(3) {
    -webkit-transform: rotate(45deg) translate(-2px, -12px);
    transform: rotate(45deg) translate(-2px, -12px);
}
html[lang="ar"] nav .toggle.change div:nth-child(1) {
    -webkit-transform: rotate(-45deg) translate(-12px, 2px);
    transform: rotate(-45deg) translate(-12px, 2px);
}
html[lang="ar"] nav .toggle.change div:nth-child(2) {
    opacity: 0;

}
html[lang="ar"] nav .toggle.change div:nth-child(3) {
    -webkit-transform: rotate(45deg) translate(-12px, -2px);
    transform: rotate(45deg) translate(-12px, -2px);
}
@media (max-width:1006px){
    nav{
        justify-content:space-between;
    }
    nav .navbar{
        display:none;
    }  
    nav .navbar{
        display: grid;
        position: fixed;
        top: 50%;
        z-index: 10;
        background-color: #fff;
        height: 70vh;
        width: 270px;
        justify-items: center;
        transition: 0.7s ease-in-out;
    }
    html[lang="en"] nav .navbar{
        right: 0px;
        transform: translate(300px, -50%);
        border-radius: 50px 0 0 50px;
    }
    html[lang="ar"] nav .navbar{
        left: 0px;
        transform: translate(-300px, -50%);
        border-radius: 0 50px 50px 0;
    }
    html[lang="en"] nav .navbar.change {
        transform: translate(0px, -50%);
        transition: 0.7s ease-in-out; 
    }
    html[lang="ar"] nav .navbar.change {
        transform: translate(0px, -50%);
        transition: 0.7s ease-in-out; 
    }
    nav .navbar ul {
        margin: 20px 20px 0 20px;
        flex-direction: column;
        align-self: center;
    }
    nav .navbar ul li {
        margin: 20px;
    }
    nav .navbar ul li a {
        color: #858585; 
    }
    nav .navbar button {
        align-self: center;
    }
    nav .toggle {
        display: block;
        width: 35px;
        margin:0 50px
    }
}

