#Home{
   width:90%;
   margin:auto;
}
#Home .content{
    margin:50px 0;
}

#Home .content .first-line, #Home .content .second-line{
    display:flex;
}

#Home .content .first-line p, #Home .content .second-line p{
    font-size:50px;
    font-weight:bold;
    margin:30px 5px;
}

#Home .content .first-line p:nth-child(1){
   background:#FBB040;
   padding:5px;
   border-radius:10px;
}

#Home .content .second-line p:nth-child(2){
   background:#0DCCA5;
   padding:5px;
   border-radius:10px;
}
#Home .content .first-line p:nth-child(3){
   position:relative;
}
 #Home .content .first-line p:nth-child(3)::after {
   content: attr(data-word1);
   position: absolute;
   top: 0px;
   overflow: hidden;
   color:#FBB040;
   animation: change-word 15s  ease-in-out infinite;
}
html[lang="en"] #Home .content .first-line p:nth-child(3)::after {
   left: 0px;
}
html[lang="ar"] #Home .content .first-line p:nth-child(3)::after {
   right: 0px;
}
@keyframes change-word {
  0% {
    content: attr(data-word1);
    width: 0px; }
  25% {
    content: attr(data-word1);
    width: 130px; }
  45% {
    content: attr(data-word1);
    width: 0px; }
  50% {
    content: attr(data-word2);
    width: 0px; }
  75% {
    content: attr(data-word2);
    width: 150px; }
  100% {
    content: attr(data-word2);
    width: 0px; } 
}
#Home .content .More {
    margin: 40px 0;
}
#Home .content .More a{
    background:#0DCCA5;
    padding:10px 220px;
    border-radius:10px;
}
